<template>
  <div class="Login">
    <div class="login_box">
      <div class="logo">
        <img src="../assets/logo.png" alt="" />
      </div>
      <div class="login_form">
        <!-- 登录表单 -->
        <el-form ref="add" :model="loginForm" class="form" :rules="rules">
          <!-- 账号框 -->
          <el-form-item prop="username">
            <el-input
              v-model="loginForm.username"
              prefix-icon="el-icon-user-solid"
            ></el-input>
          </el-form-item>
          <!-- 密码框 -->
          <el-form-item prop="password">
            <el-input
              v-model="loginForm.password"
              type="password"
              prefix-icon="el-icon-lock"
            ></el-input>
          </el-form-item>
          <!-- 登录按钮 -->
          <el-form-item>
            <el-button @click="login" type="primary">登录</el-button>
          </el-form-item>
          <el-form-item>
            <el-button
              @click="reset"
              style="background-color: grey"
              type="primary"
              class="resetting"
              >重置</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      rules: {
        username: [
          { required: true, message: '账号不能为空', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符' }
        ],
        password: [
          { required: true, message: '密码不能为空', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符' }
        ]
      }
    }
  },
  methods: {
    async login () {
      const { data: res } = await this.axios.get('/api/get/Login')
      if (res.list.username === this.loginForm.username) {
        this.$router.push('/home')
      } else {
        alert('不对不对不对')
      }
    },
    reset () {
      this.$refs.add.resetFields()
    }
  }
}
</script>
  <style scoped>
.Login {
  width: 100%;
  height: 100%;
  background-color: #333;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.logo {
  width: 130px;
  height: 130px;
  margin: -50px auto;
  border-radius: 50%;
  background-color: #fff;
  padding: 10px;
  box-shadow: 0 0 10px #666;
}
.logo img {
  width: 100%;
  border-radius: 50%;
}
.login_form {
  margin-top: 70px;
}
.form {
  padding: 0 40px;
}
.resetting {
  position: relative;
  left: 300px;
  top: -62px;
}
.Login {
  background: linear-gradient(120deg, #83c0e1 0%, #d6dce9 100%) no-repeat;
}
</style>
